.pay-with-near-not-found-page {
  &__message {
    width: 150px;
    text-align: center;
    background: #e9ebeb;
    border-radius: 10px;
    font-size: 13px;
    font-weight: 600;
    padding: 10px;
    color: #0b293d;

    &-big {
      font-size: 24px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 13px;
    color: #0b293d;
    text-decoration: underline;
  }
}
